<template>
    <div class="page-room-list flex-col">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-v-center search-bar" style="margin-right: 20px;">
                <i class="icon f-20 c-8">search</i>
                <select v-model="filter.type" class="f-14" @change="$refs.list.update(true)">
                    <option value></option>
                    <option value="1"></option>
                    <option value="2"></option>
                </select>
                <i class="icon" style="margin-left: -20px;pointer-events:none;">arrow_drop_down</i>
            </div>
            <div class="flex-item"></div>
            <router-link to="/room/add">
                <btn class="b" flat color="#008eff"></btn>
            </router-link>
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" page-num-str="currentPage" :param="filter" url="/haolifa/store-room/pageInfo" method="get">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{index}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.roomNo}}</td>
                    <td>{{types[item.type] || '-'}}</td>
                    <td>{{item.address}}</td>
                    <td>{{item.remark}}</td>
                    <td class="t-right">
                        <a href="javascript:;" class="blue" @click="edit(item)" style="margin-right: 3px;"></a> |
                        <a href="javascript:;" class="red" @click="remove(item)" style="margin-right: 3px;"></a>
                    </td>
                </template>
            </data-list>
        </div>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "page-room-list",
    components: { DataList },
    data() {
        return {
            types: ["", "", ""],
            filter: {
                type: ""
            }
        };
    },
    methods: {
        flush() {
            this.filter.type = "";
            this.$refs.list.update(true);
        },
        edit(item) {
            this.$router.push(`/room/edit?id=${item.id}`);
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br><b>${item.name}</b>`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .delete(`/haolifa/store-room/delete/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg);
                        });
                }
            });
        }
    }
};
</script>

<style lang="less">
.page-room-list {
    select {
        background: none;
        border: none;
        outline: none;
        padding: 5px 20px 5px 10px;
        appearance: none;
    }
    .scroll-y {
        padding-bottom: 40px;
    }
}
</style>
